@media screen and (max-width: 1880px) {
    .innovative-projects-box .tlr-stack {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (max-width: 1880px) {
    .widget-table thead th {
        font-size: 1.4rem;

    }
}

@media screen and (max-width: 1680px) {
    .progress-widget {
        .progress-widget__caption {
            font-size: 1.6rem;
        }
    }

    .sub-btn .sub-btn__caption {
        font-size: 1.6rem;
    }

    .project-group .project-group__line {
        font-size: 1.4rem;
    }

    .innovative-projects-box .status-cell-stack {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 1550px) {
    .control-panel-areas-of-expertise {
        grid-template-columns: repeat(3, 1fr);
    }
    .trl {
        span{
            &:not(:last-child) {
                margin-right: 0.4rem;
            }
        } 
    }
}

@media screen and (max-width: 1510px) {
    .project-top-panel {
        position: relative;
        justify-content: space-between;
        // flex-wrap: wrap;
    }

    .project-top-panel {
        padding-bottom: 4.6rem;

        .progress-widget {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 3.8rem;
            margin-left: 0;
            font-size: 1.8rem;
        }

        .project-group {
            width: 25%;
            justify-content: center;
        }

        .project-top-panel__item {
            width: 15%;
        }
    }

    .team-table {
        tbody {
            td:last-child {

                .inner-table-btn,
                .trachcan-btn {
                    margin-left: 1rem;
                }
            }
        }
    }
}
@media screen and (max-width: 1480px) {
    .media-news {
        grid-template-columns: repeat(1, 1fr);
    }
}
@media screen and (max-width: 1450px) {
    .innovative-projects-box {
        grid-template-columns: repeat(1, 1fr);
    }

    .scientific-projects-box {
        grid-template-columns: repeat(1, 1fr);
    }
    .trl {
        span{
            &:not(:last-child) {
                margin-right: 0.8rem;
            }
        } 
    }
}

@media screen and (max-width: 1400px) {
    .media-news {
        grid-template-columns: repeat(1, 1fr);
    }
}

@media screen and (max-width: 1380px) {
    .dashboard-areas-of-expertise {
        grid-template-columns: 1fr 1fr;
    }

    .video-collection {
        grid-template-columns: repeat(2, 1fr);
    }

}

@media screen and (max-width: 1230px) {
    .menu {
        flex-wrap: wrap;
        li {
            &:not(:last-child) {
                margin-right: 1.4rem;
            }

            a {
                line-height: 28px;
            }
        }
    }
    .control-panel {
        .menu-additional{
            .btn {
                margin-left: 10px;
            }
        }
    }
    .btn{
        font-size: 1.4rem;
        padding: 0.5rem 0.5rem;
        &--with-elem{
            svg{
                margin-right: 0;
            }
        }
    }
}

@media screen and (max-width: 1320px) {


    .project-top-panel {
        padding-bottom: 0;

        .progress-widget {
            position: relative;
            bottom: 0;
            left: 0;
            width: auto;
            height: 3.8rem;
            margin-left: 0.8;
            font-size: 1.8rem;
        }

        .project-group {
            width: auto;
            justify-content: center;
        }

        .project-top-panel__item {
            width: 3.8rem;
        }
    }

    .innovative-projects-box .status-cell-stack {
        grid-template-columns: repeat(4, 1fr);
    }

    .innovative-projects-box .tlr-stack {
        grid-template-columns: repeat(6, 1fr);
    }
}

@media screen and (max-width: 1160px) {
    .dashboard-areas-of-expertise {
        grid-template-columns: 1fr;
    }
}
@media screen and (max-width: 1120px) {
    .control-panel-areas-of-expertise { 
        grid-template-columns: repeat(2,1fr);
    }
}
@media screen and (max-width: 1110px) {
    .innovative-projects-box .status-cell-stack {
        grid-template-columns: repeat(2, 1fr);
    }

    .innovative-projects-box .tlr-stack {
        grid-template-columns: repeat(3, 1fr);
    }
}
@media screen and (max-width: 999px){
    .video-collection { 
        grid-template-columns: repeat(1,1fr);
    }
}

