.right-modal {
    position: fixed;
    top: 0;
    right: -2000px;
    max-width: 607px;
    width: 100%;
    height: 100%;
    background-color: var(--white);
    padding: 4rem;
    padding-right: 2rem;
    box-sizing: border-box;
    z-index: 510;
    transition: var(--great-transition);

    &--show {
        right: 0;
    }

    .close-modal-btn {
        position: absolute;
        left: -5.6rem;
        top: 3.2rem;
        transition: var(--great-transition);
        transform: scale(1);

        &:hover {
            transform: scale(1.1);
            // animation-name: gurgling;
            // animation-duration: 300ms;
            // animation-timing-function: ease;
            // animation-iteration-count: 1;
            // animation-delay: 0;
            // animation-duration: normal;

        }
    }

    .right-modal__content {
        position: relative;
        display: flex;
        flex-direction: column;
        height: 100%;
        padding-right: 2rem;
        box-sizing: border-box;
        overflow: auto;
        iframe{
            body{
                padding-right: 2rem;
            }
        }
    }

    h2 {
        margin-bottom: 2.4rem;
        font-size: 2.4rem;
        line-height: 1.2;
        font-weight: 600;
    }

    p {
        margin: 1.6rem 0;
        font-size: 1.8rem;
        color: var(--dark-grey);
    }
}

.close-modal-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: var(--red);
    cursor: pointer;

    svg {
        display: block;
        width: 1rem;
        height: 1rem;

        path {
            fill: var(--white);
        }
    }
}

